.btn {
  padding: 1rem 1.5rem;
  background-color: #999;
  border: transparent;
  border-radius: 3px;
  font-size: 1.4rem;
  cursor: pointer;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
  color: #fff;
}

.btn:hover {
  opacity: 0.8;
}



